import React, {useState} from 'react'
import { Row,Col,Nav,Tab } from 'react-bootstrap'
 import AdvanceSearchOption from '../../../../Global/App/AdvanceSearchOption/AdvanceSearchOption.component'
const Banner = () => {

    return (
        <section className="home-one home1-overlay home1_bgi1">
            <div className="container">
                <div className="row posr">
                    <div className="col-lg-12">
                        <div className="home_content">
                            <div className="home-text text-center">
                                <h2 className="fz55">Find Your Dream Home</h2>
                                <p className="fz18 color-white">From as low as $10 per day with limited time offer discounts.</p>
                            </div>
                            <div className="home_adv_srch_opt">
                                <Tab.Container id="left-tabs-example" defaultActiveKey="buy">
                                    <Row>
                                        <Col sm={12}>
                                            <Nav id="pills-tab" className="nav nav-pills">
                                                <Nav.Item>
                                                    <Nav.Link eventKey="buy">Buy</Nav.Link>
                                                </Nav.Item>
                                                <Nav.Item>
                                                    <Nav.Link eventKey="rent">Rent</Nav.Link>
                                                </Nav.Item>
                                            </Nav>
                                        </Col>
                                        <Col sm={12}>
                                            <Tab.Content id="pills-tabContent" className="home1_adsrchfrm">
                                                <Tab.Pane eventKey="buy">
                                                    <AdvanceSearchOption index="1" />
                                                </Tab.Pane>
                                                <Tab.Pane eventKey="rent">
                                                <AdvanceSearchOption  index="2" />
                                                </Tab.Pane>
                                            </Tab.Content>
                                        </Col>
                                    </Row>
                                </Tab.Container>
                            </div>
                        </div>      
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Banner