import React from "react";
import Layout from '../components/Global/Layout/Layout'
import HeaderHomeOne from "../components/Local/Home/HomeOne/Header/Header.HomeOne.component";
import Banner from '../components/Local/Home/HomeOne/Banner/Banner.local.component'
import FeatureProperties from '../components/Global/Home/FeatureProperties/FeatureProperties.component'
import PropertyCities from '../components/Local/Home/HomeOne/PropertyCities/PropertiesCities.component'
import WhyChooseUs from '../components/Global/WhyChooseUs/WhyChooseUs.component'
import Testimonials from '../components/Global/Testimonials/Testimonials.component'
import OurBlogComponent from '../components/Global/Home/OurBlog/OurBlog.component'
import OurPartners from '../components/Global/OurPartners/OurPartners.component'
import StartPartners from '../components/Global/StartPartners/StartPartners.component'
import Footer from '../components/Global/App/Footer/Footer.component'

const Home = () => {
	return (
		<Layout>
			<HeaderHomeOne/>
			<Banner />
			<FeatureProperties>Featured Properties</FeatureProperties>
			<PropertyCities />
			<WhyChooseUs addClass="bgc-f7"/>
			<Testimonials addClass='our-testimonial' textColor='color-white'/>
			<OurBlogComponent />
			<OurPartners />
			<StartPartners addClass="bgc-thm"/>
			<Footer />
		</Layout>
	);
}

export default Home